.card {
  color: white;
  position: relative;
  width: 100vw;
  height: 100vh;
}
.copy,.nav,.cardCounter,.card img {
  position: absolute;
}
.card img {
  z-index: -10;
  object-fit: cover;
  width: 100vw;
  height: 100vh;
}
.nav {
  color: white;
  height: 20px;
  padding: 5%;
  top:0;
  left: 0;
  background-image: url("images/GradientBG2.png");
  background-size: 100% 100%;
}
.copy {
  width: 50%;
  padding: 5%;
  padding-top: 20%;
  left: 0px;
  bottom: 0px;
  background-image: url("images/GradientBG.png");
  background-size: 100% 100%;
}
.cardCounter {
  bottom: 0px;
  right: 0px;
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
  text-align: right;
  padding: 1% 5%;
  margin: 5%;
}
h1,p {
  position: static;
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 1.75rem;
}
h3 {
  font-size: 1rem;
  display: inline;
}
p {
  font-size: 1rem;
  font-weight: 200;
}